* {
    -webkit-box-sizing: border-box;
            box-sizing: border-box;
}

.app-header {
    -ms-flex-align: center;
        align-items: center;
    background-color: black;
    color: white;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: column;
        flex-direction: column;
    -ms-flex-pack: center;
        justify-content: center;
    min-height: 40px;
    padding: 5px;
    position: fixed;
    top: 0;
    width: 100%;
}

.logo {
    padding: 10px;
}

.search-container {
    position: relative;
    width: 75%;
}

.search {
    border: none;
    border-radius: 4px;
    display: -ms-flexbox;
    display: flex;
    -ms-flex: 1 1;
        flex: 1 1;
    font-size: var(--default-font-size);
    min-height: 20px;
    padding: 8px 10px;
    width: 100%;
}

.search-icon {
    color: #cccccc;
    position: absolute;
    right: 8px;
    top: 8px;
}

/*MEDIA QUERIES*/

@media (min-width: 700px) {
    .app-header {
        -ms-flex-direction: row;
            flex-direction: row;
        -ms-flex-pack: distribute;
            justify-content: space-around;
    }

    .search-container {
        -ms-flex-item-align: center;
            align-self: center;
        width: 50%;
    }
}



