.comic-card {
    -ms-flex-align: start;
        align-items: flex-start;
    background-color: var(--white);
    -webkit-box-shadow: var(--box-shadow);
            box-shadow: var(--box-shadow);
    display: -ms-flexbox;
    display: flex;
    -ms-flex-preferred-size: 45%;
        flex-basis: 45%;
    -ms-flex-wrap: wrap;
        flex-wrap: wrap;
    font-family: var(--roboto);
    -ms-flex-pack: justify;
        justify-content: space-between;
    margin: var(--default-font-size);
    padding: 10px;
}

.comic-image, 
.comic-desc-container {
    -ms-flex-preferred-size: 50%;
        flex-basis: 50%;
}

.comic-image {
    border-radius: 50%;
    -webkit-box-shadow: var(--box-shadow);
            box-shadow: var(--box-shadow);
    height: var(--image-size);
    margin-left: -25px;
    margin-top: -25px;
    width: var(--image-size);
}

.comic-desc-container {
    margin-top: -22px;
    padding-left: var(--default-font-size);
    text-align: left;
}

h2 {
    font-size: 20px;
    font-weight: 500;
    margin: 0;
    padding: 0;
    text-transform: uppercase;
}

p {
    color: var(--dark-gray);
    font-weight: 300;
    font-size: 16px;
}



